.gallery {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  height: 100%;
  padding: 97px 0px 80px 0px;
  background: -webkit-linear-gradient(180deg, rgb(255, 245, 246) 45%, rgb(255, 255, 255) 2%);
  background: -moz-linear-gradient(180deg, rgb(255, 245, 246) 45%, rgb(255, 255, 255) 2%);
  background: linear-gradient(180deg, rgb(255, 245, 246) 45%, rgb(255, 255, 255) 2%);
  &__section {
    max-width: 1280px;
  }
}
@media screen and (min-width: 768px) {
  .gallery {
    padding: 140px 0px 120px 0px;
  }
}
